<template>
  <section class="experience">
    <h2><img src="/assets/work.png" alt="icon" class="icon">Experience</h2>
    <!-- <p>{{ jobs.period }}</p> -->
    <ul>
      <li v-for="(job, index) in computedJobs" :key="index">
        <div class="job-content">
          <img :src="job.company_logo" alt="icon" class="logo">
          <div class="job-text">
            <strong class="company">{{ job.company }}</strong>
            <p class="company_sub">{{ job.job_type }} | {{ job.job_period }}</p>
          </div>
        </div>
        <div v-for="(title, i) in job.titles" :key="i">
          <h3 class="job_title">{{ title.text }} | {{ title.period }}</h3>
          <ul v-if="title.descripe">
            <li class="describe" v-for="(item, j) in title.descripe.split(';')" :key="j">
              {{ item }}
            </li>
          </ul>
        </div>
      </li>
    </ul>
    <div v-if="!jobs || jobs.length === 0">
      No job experience available.
    </div>
  </section>
</template>

<script>
export default {
  props: {
    jobs: {
      type: Array,
      required: true,
    },
  },
  computed: {
    computedJobs() {
      return this.jobs.map((job) => {
        const titles = Object.keys(job)
          .filter((key) => key.startsWith("title"))
          .map((key) => ({
            text: job[key], // Title 文本
            period: job[`period_${key.split("_")[1]}`] || "N/A",
            descripe: job[`descripe_${key.split("_")[1] || ""}`], // 對應的描述
          }));
        return {
          ...job,
          titles, // 將處理好的 titles 添加到 job 對象
        };
      });
    },
  },
};
</script>

<style scoped>
.experience h2 {
  border-bottom: 2px solid #007bff;
}
.experience h2 .icon {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  /* vertical-align: middle; */
}

.logo {
  width: 50px;
  height: 50px;
  object-fit: cover; /* 確保圖片不變形 */
  border-radius: 5px; /* 圖片圓角效果 */
  /* vertical-align: middle; */
}

.job-content{
  display: flex; /* 使用 Flexbox 佈局 */
  align-items: center; /* 垂直居中 */
}

.job-text {
  margin-left: 10px;
  display: flex;
  flex-direction: column; /* 文字垂直排列 */
}

.company {
  display: block; /* 確保內縮適用於整行 */
  /*padding-left: 20px; /* 調整內縮距離 */
  /* text-indent: 10px; /* 或使用文字內縮 */
  color: #555; /* 選擇性：修改文字顏色 */
}

.company_sub {
  display: block; /* 確保內縮適用於整行 */
  /*padding-left: 20px; /* 調整內縮距離 */
  /*text-indent: 10px; /* 或使用文字內縮 */
  color: #555; /* 選擇性：修改文字顏色 */
}

.job_title{
  display: block;
  padding-left: 50px;
}

.describe {
  display: block; /* 確保內縮適用於整行 */
  padding-left: 60px; /* 調整內縮距離 */
  color: #555; /* 選擇性：修改文字顏色 */
  text-align: justify;
}

.describe::before {
  content: '>'; /* 使用箭頭符號 */
  position: absolute; /* 絕對定位箭頭 */
  left: 45px; /* 將箭頭放置在列表左側 */
  color: #000000; /* 自定義箭頭顏色 */
  font-size: 1em; /* 調整箭頭大小 */
}
</style>