<template>
  <section class="Educations">
    <h2><img src="/assets/education.png" alt="icon" class="icon">Educations</h2>
    <ul>
      <li v-for="(education, index) in educations" :key="index">
        <div class="education-content">
          <img :src="education.degree_logo" alt="icon" class="logo">
          <div class="education-text">
            <strong class="company">{{ education.degree }}</strong>
            <p class="company_sub">{{ education.school }} | {{ education.job_period }}</p>
          </div>
        </div>
        <div v-for="(title, i) in education.descripes" :key="i">
          <ul v-if="title.descripe">
            <li class="describe" v-for="(item, j) in parseDescription(title.descripe)" :key="j" v-html="item"></li>
          </ul>
        </div>
      </li>
    </ul>

    <div v-if="!educations || educations.length === 0">
      No education available.
    </div>
  </section>
</template>

<script>
export default {
  props: {
    educations:{
      type: Array,
      required: true,
    },
  },

  methods: {
    // 將描述文字解析為 HTML，支持超連結格式 [text](url)
    parseDescription(descripe) {
      return descripe.split(";").map((text) => {
        return text.replace(
          /\[([^\]]+)\]\((https?:\/\/[^)]+)\)/g, // 修正正則表達式
          '<a href="$2" class="custom-link">$1</a>'
        );
      });
    },
  },

  mounted() {
    console.log("Received educations:", this.educations); // 調試接收數據
  },
};
</script>

<style scoped>
.logo {
  width: 50px;
  height: 50px;
  object-fit: cover; /* 確保圖片不變形 */
  border-radius: 5px; /* 圖片圓角效果 */
  /* vertical-align: middle; */
}


.Educations h2 {
  border-bottom: 2px solid #007bff;
}

.Educations h2 .icon {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  /* vertical-align: middle; */
}

.education-content{
  display: flex; /* 使用 Flexbox 佈局 */
  align-items: center; /* 垂直居中 */
}

.education-text {
  margin-left: 10px;
  display: flex;
  flex-direction: column; /* 文字垂直排列 */
}

.company {
  display: block; /* 確保內縮適用於整行 */
  /*padding-left: 20px; /* 調整內縮距離 */
  /* text-indent: 10px; /* 或使用文字內縮 */
  color: #555; /* 選擇性：修改文字顏色 */
}

.company_sub {
  display: block; /* 確保內縮適用於整行 */
  /*padding-left: 20px; /* 調整內縮距離 */
  /*text-indent: 10px; /* 或使用文字內縮 */
  color: #555; /* 選擇性：修改文字顏色 */
}

.education_title{
  display: block;
  padding-left: 50px;
}

.describe {
  display: block; /* 確保內縮適用於整行 */
  padding-left: 60px; /* 調整內縮距離 */
  color: #555; /* 選擇性：修改文字顏色 */
}

.describe::before {
  content: '>'; /* 使用箭頭符號 */
  position: absolute; /* 絕對定位箭頭 */
  left: 45px; /* 將箭頭放置在列表左側 */
  color: #000000; /* 自定義箭頭顏色 */
  font-size: 1em; /* 調整箭頭大小 */
}

.custom-link {
  color: gray;           /* 設置文字顏色為灰色 */
  text-decoration: underline; /* 保留底線 */
}

.custom-link:hover {
  color: darkgray;       /* 滑鼠懸停時改變顏色（可選） */
}
</style>