<template>
  <section class="skills">
    <h2><img src="/assets/skills.png" alt="icon" class="icon">Skills</h2>
    <!-- 遍歷分組後的技能 -->
    <div v-for="(group, category) in groupedSkills" :key="category" class="skill-group">
      <h3 class="category">{{ category }}</h3>
      <div class="skills-row">
        <div v-for="(skill, index) in group" :key="index" class="skill-item">
          <i v-if="skill.icon === 'ros'" :class="`devicon-${skill.icon}-original`"></i>
          <i v-else-if="skill.icon === 'qt'" :class="`devicon-${skill.icon}-original`"></i>
          <i v-else-if="skill.icon === 'tensorflow'" :class="`devicon-${skill.icon}-original`"></i>
          <i v-else :class="`devicon-${skill.icon}-plain`"></i>
          <span>{{ skill.name }}</span>
        </div>
      </div>
    </div>

    <!-- 如果技能數據為空 -->
    <div v-if="!skills || skills.length === 0"> 
      No skills available.
    </div>
  </section>
</template>

<script>
export default {
  props: {
    skills: {
      type: Array,
      required: true,
    },
  },
  computed: {
    groupedSkills() {
      // 將技能按 skill 分類
      return this.skills.reduce((groups, skill) => {
        if (!groups[skill.skill]) {
          groups[skill.skill] = [];
        }
        groups[skill.skill].push(skill);
        return groups;
      }, {});
    },
  },
  mounted() {
    console.log("Received skills:", this.skills); // 調試接收數據
  },
};
</script>

<style scoped>
.skills h2 {
  border-bottom: 2px solid #007bff;
  font-size: 20px;
  margin-bottom: 16px;
}
.skills h2 .icon {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  /* vertical-align: middle; */
}

.skill-group {
  margin-bottom: 24px;
}

.category {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin-bottom: 8px;
}

.skills-row {
  display: flex; /* 使用 flexbox 實現水平排列 */
  flex-wrap: wrap; /* 如果太多內容自動換行 */
  gap: 16px; /* 控制技能卡片之間的間距 */
}

.skill-item {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.skill-logo {
  width: 24px;
  height: 24px;
  object-fit: contain;
  font-size: 20px;
}
</style>