<template>
  <div id="app">

    <!-- 固定在頁面頂部的 tool_bar -->
    <div class="tool-bar">
      <span>Chen, Chun-Ting</span>
      <!-- 可以加入按鈕或其他工具 -->
      <link rel="stylesheet" type='text/css' href="https://cdn.jsdelivr.net/gh/devicons/devicon@latest/devicon.min.css" />
      <div class="language-switcher">
        <button v-for="lang in languages" :key="lang" @click="switchLanguage(lang)">
          {{ lang.toUpperCase() }}
        </button>
    </div>
    <button @click="exportToPDF">Export to PDF</button>
  </div>
    <Header v-if="headerData" :data="headerData" />
    <div class="content-layout">
      <div class="left-column">
        <Experience v-if="experienceData" :jobs="experienceData" />
        <Education v-if="educationData" :educations="educationData" />
      </div>
      <div class="right-column">
        <Certification v-if="certificationData" :certifications="certificationData" />
        <Skills v-if="skillsData" :skills="skillsData" />
        
        </div>
    </div>
  </div>
</template>

<script>
import Header from "./components/UserHeader.vue";
import Experience from "./components/UserExperience.vue";
import Certification from "./components/UserCertification.vue";
import Skills from "./components/UserSkills.vue";
import Education from "./components/UserEducation.vue";
import html2pdf from "html2pdf.js";

export default {
  components: {
    Header,
    Experience,
    Certification,
    Skills,
    Education,
  },
  data() {
    return {
      headerData: null, // 用於存儲從 JSON 讀取的 Header 數據
      experienceData: null,
      certificationData: null,
      skillsData: null,
      educationData:null,
      currentLanguage: "en", // 默認語言
      languages: ["en", "zh"], // 可切換的語言列表
      translations: {
        en: {
          N: ["JavaScript", "Vue.js", "HTML", "CSS"],
        },
        zh:{
          N: ["JavaScript", "Vue.js", "HTML", "CSS"],
        },
      },
    };
  },
  computed: {
    texts() {
      return this.translations[this.currentLanguage];
    },
  },
  methods: {
    async switchLanguage(lang) {
      this.currentLanguage = lang;
      await this.loadHeaderData();
      await this.loadExperienceData();
      await this.loadCertificationData();
      await this.localizedSkills();
      await this.localizedEducation();
    },
    async loadHeaderData() {
      try {
        const filePath = `/assets/content/header_${this.currentLanguage}.json` ; // 根據語言加載對應 JSON
        const response = await fetch(filePath);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        this.headerData = await response.json();
      } catch (error) {
        console.error("Failed to load header data:", error);
      }
    },

    async loadExperienceData(){
      try {
        const filePath = `/assets/content/experience_${this.currentLanguage}.json` ; // 根據語言加載對應 JSON
        const response = await fetch(filePath);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        this.experienceData = await response.json();
      } catch (error) {
        console.error("Failed to load experience data:", error);
      }

    },
    async loadCertificationData(){
      try {
        const filePath = `/assets/content/certification_${this.currentLanguage}.json` ; // 根據語言加載對應 JSON
        const response = await fetch(filePath);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        this.certificationData = await response.json();
      } catch (error) {
        console.error("Failed to load certification data:", error);
      }

    },
    async localizedSkills(){
      try {
        const filePath = `/assets/content/skills.json` ; // 根據語言加載對應 JSON
        const response = await fetch(filePath);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        this.skillsData = await response.json();
      } catch (error) {
        console.error("Failed to load skill data:", error);
      }
    },
    async localizedEducation(){
      try {
        const filePath = `/assets/content/education_${this.currentLanguage}.json` ; // 根據語言加載對應 JSON
        const response = await fetch(filePath);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        this.educationData = await response.json();
      } catch (error) {
        console.error("Failed to load education data:", error);
      }
    },
    exportToPDF() {
      // 獲取需要匯出的 DOM 節點
      const element = document.getElementById('app');
      
      // 配置 pdf 選項
      const options = {
        margin: [10,20,10,20],
        filename: 'Chen-ChunTint_CV.pdf',
        image: { type: 'jpeg', quality: 0.75},
        html2canvas: { scale: 5 },
        jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' },
      };

      // 使用 html2pdf 生成 PDF
      html2pdf().set(options).from(element).save();
    }
  },
  mounted() {
    this.loadHeaderData(); // 初始化讀取當前語言的 JSON 文件
    this.loadExperienceData(); 
    this.loadCertificationData(); 
    this.localizedSkills();
    this.localizedEducation();
  },
};
</script>
